import request from '../utils/request'

export const fetchTab = () => request('/tab')

export const fetchImage = () => request('/tabImage')

export const fetchkeyword = () => request('/twitter')

export const deletekeyword = (keyword) => request('/twitter/delete', {
    keyword: keyword
}, 'POST')
export const addkeyword = (keyword) => request('/twitter/add', {
    keyword: keyword
}, 'POST')
export const search = (keyword) => request('/twitter/search', {
    keyword: keyword
}, 'POST')



