<template>
    <section>
      <input  class="input" type="text" v-model="value">
      <div class="operation">
        <button @click="add">增加</button> 
        <button @click="deletekey">删除</button>
        <button @click="selectAll">全选</button>
        <button @click="search">搜索</button>

      </div>
      <ul class="select" >
         <li v-for="(item,key) in arr" :key="key"><input type="checkbox" :value="item" v-model="value2" />{{item}}</li>
      </ul>
      <div class="keyword">搜索/删除的关键词：{{value2}}</div>
    </section>
</template>

<script>
import {fetchkeyword, deletekeyword,search,addkeyword} from '@/api/service'
export default {
  data () {
    return {
      arr: [],
      value: "",
      value2: []
    }
  },

  components: {},

  computed: {},

  created () {
    this.fetch()
  },

  mounted () {
  },

  methods: {
    fetch() {
      fetchkeyword().then((res) => {
        for(let item of res) {
          this.arr.push(item.data.keyword)
        }
      })
    },
    add() {
      console.log(this.value)
      addkeyword(this.value).then((res)=>{
        this.arr = []
        this.value = ''
        this.fetch()
      })
    },
    deletekey(){
      deletekeyword(this.value2).then((res)=>{
        this.arr = []
        this.value2 = []
        this.value = ''
        this.fetch()
      })
    },
    selectAll() {
      this.value2 === this.arr ? this.value2 = [] : this.value2 = this.arr
    },
    search() {
      search(this.value2).then((res) => {
        console.log(res)
      })
    }
  },

  watch: {}
}

</script>

<style lang='scss' scoped>
.input {
  position: absolute;
  top: 10%;
  left: 10%;
  height: 30px;

}
.operation {
  position: absolute;
  top: 10%;
  left: 30%;
} 
button {
  height:30px;
  width: 50px;
}
.select {
  position: absolute;
  top: 20%;
  left: 10%;
}
li{
  display: inline-block;
  width: 200px;
  height:100px;
  line-height: 30px;
  font-size: 20px;
}
.keyword{
  position: absolute;
  top: 30%;
  left: 10%;
}

</style>
 


